body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /*background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);*/
  /*background: linear-gradient(to right, rgb(22, 208, 167), rgb(194, 124, 202));*/
  background: -webkit-linear-gradient(left, rgb(72, 247, 238), rgb(223, 207, 166));
  background: linear-gradient(to right, rgb(72, 247, 238), rgb(223, 207, 166));
}



.Tilt {
	background: -webkit-linear-gradient(left, rgb(72, 247, 238), rgb(223, 207, 166));
	background: linear-gradient(to right, rgb(72, 247, 238), rgb(223, 207, 166));
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

